.sale {
  text-align: center;
}

.sale__text-content {
  color: #1C3374;
}

.sale__text-content h2 {
  color: #102654;
  font-family: $ff-base;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0;
  margin-bottom: 70px;
  display: block;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin-bottom: 40px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin-bottom: 42px;
  }
}

.sale__text-content ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: left;

  @include vp-1365 {
    gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
}

.sale__text-content li {

  @include vp-1365 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.sale__text-content li:first-of-type {
  grid-row: 1 / 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #ED0233;
  color: $color-default-white;

  @include vp-1365 {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    display: flex;
    flex-direction: row;
  }

  @include vp-767 {
    flex-direction: column;
    margin-bottom: 10px;
  }
}

.sale__wrapper-text-content {
  padding: 45px;
  padding-top: 40px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include vp-1365 {
    max-width: 279px;
  }

  @include vp-767 {
    max-width: none;
    text-align: center;
    padding: 45px 50px 50px;
  }
}

.sale__wrapper-text-content b {
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  margin-bottom: 25px;
  display: block;

  @include vp-1365 {
    margin-bottom: 20px;
  }

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
  }
}

.sale__wrapper-text-content a,
.sale__text-content a {
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  display: block;
}

.sale__text-content a {
  color: #1C3374;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 100%;
    max-width: 79px;
    height: 1px;
    background-color: #1C3374;
    position: absolute;
    top: 19px;
    left: 40px;

    @include vp-767 {
      top: 17px;
      left: 46px;
    }
  }
}

.sale__wrapper-text-content a {
  color: $color-default-white;

  &::after {
    background-color: $color-default-white;

    @include vp-767 {
      top: 16px;
      left: 56px;
    }
  }
}

.sale__text-content h3 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 10px;
  letter-spacing: 1px;

  @include vp-1365 {
    letter-spacing: 0;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
  }
}

.sale__text-content p {
  font-weight: 300;
  margin: 0;
  margin-bottom: 67px;

  @include vp-1365 {
    margin-bottom: 45px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 31px;
  }
}

.sale__wrapper-text-content h3 {
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  margin: 0;
  margin-bottom: 5px;

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 9px;
  }
}

.sale__wrapper-text-content p {
  margin: 0;
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 174px;

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 35px;
  }
}

.sale__text-content li:not(:first-of-type) {
  border: 7px solid;
  padding: 23px 28px 33px;
  border-image: linear-gradient(to right,rgba(209, 10, 66, 1),rgba(0, 53, 159, 1))1;

  @include vp-767 {
    padding: 38px 53px 37px;
    text-align: center;
  }
}
